<p><span class="rvts58">死灵学派（</span><span class="rvts59">Necromancer</span><span class="rvts58">）</span><span class="rvts59">:</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber334.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">死灵派系拥有</span><span class="rvts20">17</span><span class="rvts21">个技能。死灵系拥有很多治疗技能甚至可以治疗不死族。</span>
<br><span class="rvts21">另外，死灵系提供额外的伤害回血，这些在战斗中是比较实用的。然而，死灵法师并不能从死灵派系技能中获得额外伤害加成。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">死灵学派技能书可以在以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到：</span>
<br><span class="rvts20">1. </span><span class="rvts21">莫纳（</span><span class="rvts20">Mona</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">科本（</span><span class="rvts20">Kerban</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">3. </span><span class="rvts21">塔昆（</span><span class="rvts20">Tarquin</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">复仇女士号</span>
<br><span class="rvts20">4. </span><span class="rvts21">商人布理（</span><span class="rvts20">Trader Bree</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">5. </span><span class="rvts21">恩雅（</span><span class="rvts20">Eithne</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">回廊森林，死神海岸</span>
<br><span class="rvts20">6. </span><span class="rvts21">黑环炼金师（</span><span class="rvts20">Black Ring Alchemist</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">7. </span><span class="rvts21">乔邦（</span><span class="rvts20">Jawbone</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">8. </span><span class="rvts21">商人阿若瓦伊（</span><span class="rvts20">Trader Avarae</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
<br><span class="rvts20">9. </span><span class="rvts21">商人诺恩（</span><span class="rvts20">Trader Non</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
<br><span class="rvts20">10. </span><span class="rvts21">胜利者沃斯（</span><span class="rvts20">Victor Voss</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
